html {
  font-size: 100%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  height: 55.4em;
  margin: 0;
  padding: 0;
  background-color: var(--l-grayish-blue);
  display: flex;
  justify-content: center;
  align-items: center;
}

:root {
  --v-dark-grayish-blue: hsl(217, 19%, 35%);
  --d-dark-blue: hsl(214, 17%, 51%);
  --grayish-blue: hsl(212, 23%, 69%);
  --l-grayish-blue: hsl(210, 46%, 95%);
  /*
    v - very
    d - desaturated
    l - light
  */
}

:root {
  --font-manrope: "Manrope", sans-serif;
}

h1, h2, h3, h4, p {
  font-family: var(--font-manrope);
  margin: 0;
}

.hero {
  display: flex;
  width: 70em;
  height: 23rem;
  margin: 0 15rem;
  border-radius: 15px;
  background: white;
  box-shadow: 0px 0px 100px 5px rgba(109, 127, 151, 0.3);
}
.hero__img {
  flex: 1;
}
.hero__img img {
  display: block;
  width: 100%;
  height: 100%;
}
.hero__post {
  flex: 1.2;
  padding: 1.5rem 4rem;
}
.hero__post .title {
  margin-top: 1.5rem;
  font-size: 1.6rem;
  color: var(--v-dark-grayish-blue);
}
.hero__post .description {
  margin-top: 1rem;
  color: var(--d-dark-blue);
  line-height: 1.5;
}
.hero__post .contact {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-top: 2rem;
}
.hero__post .contact .profile {
  display: flex;
}
.hero__post .contact .profile__photo {
  width: 3em;
  border-radius: 50%;
}
.hero__post .contact .profile__text {
  margin-left: 0.5rem;
}
.hero__post .contact .profile__text .name {
  color: var(--v-dark-grayish-blue);
  font-size: 1rem;
  font-weight: 700;
}
.hero__post .contact .profile__text .date {
  font-size: 0.9rem;
  font-weight: 500;
  color: var(--grayish-blue);
}
.hero__post .contact .share {
  position: relative;
}
.hero__post .contact .share__photo {
  color: var(--v-dark-grayish-blue);
  background-color: var(--l-grayish-blue);
  border-radius: 50%;
  padding: 0.7rem;
  font-size: 0.9rem;
  cursor: pointer;
}
.hero__post .contact .share__triangle {
  position: absolute;
  display: none;
  top: -2.5em;
  left: 0.35rem;
}
.hero__post .contact .share__triangle i {
  font-size: 2.7rem;
  color: var(--v-dark-grayish-blue);
}
.hero__post .contact .share__container {
  position: absolute;
  display: none;
  width: 15em;
  align-items: center;
  justify-content: space-between;
  background-color: var(--v-dark-grayish-blue);
  padding: 0.8rem 2rem;
  border-radius: 10px;
  top: -4em;
  left: -6.1em;
}
.hero__post .contact .share__container p {
  color: var(--grayish-blue);
  text-transform: uppercase;
  letter-spacing: 4px;
  font-size: 0.8rem;
}
.hero__post .contact .share__container i {
  cursor: pointer;
  color: var(--l-grayish-blue);
}